import * as React from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepButton from '@mui/material/StepButton';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { motion } from 'framer-motion';
import { makeStyles } from '@mui/styles';
import { keyframes } from '@mui/styled-engine';
import './App.css';
import './index.css';


const steps = ['PROJECT CAPABILITY', 'DISCIPLINES', 'PORTFOLIO CAPABILITY - LIFESTYLE', 'PORTFOLIO CAPABILITY - WORKPLACE', 'PORTFOLIO CAPABILITY - COMMUNITY'];

const useStyles = makeStyles({
    mystyle :{
             
        "& .css-1u4zpwo-MuiSvgIcon-root-MuiStepIcon-root.Mui-active" : {
            color: "red", 
         },
        "& .css-1u4zpwo-MuiSvgIcon-root-MuiStepIcon-root.Mui-completed": {
             color: "green", 
          },
        "& .MuiSvgIcon-root.MuiStepIcon-root.Mui-active.css-4ff9q7" :{
            color: "red !important", 
         },
         "& .MuiSvgIcon-root.MuiStepIcon-root.Mui-completed.css-4ff9q7" :{
            color: "green !important", 
         },        
          
    }
    
});

export default function HorizontalNonLinearStepper({setCurrentStep}) {
  const [activeStep, setActiveStep] = React.useState(0);
  const [completed, setCompleted] = React.useState({});

  console.log(' <<< activeStep >>> ',activeStep)
  console.log(' <<< completed >>> ',completed)
  console.log(' <<< steps >>> ',steps) 

  const classes = useStyles(); 

  

  const totalSteps = () => {
    return steps.length;
  };

  const completedSteps = () => {
    return Object.keys(completed).length;
  };

  const isLastStep = () => {
    return activeStep === totalSteps() - 1;
  };

  const allStepsCompleted = () => {
    return completedSteps() === totalSteps();
  };

  const handleNext = () => {
    const newActiveStep =
      isLastStep() && !allStepsCompleted()
        ? // It's the last step, but not all steps have been completed,
          // find the first step that has been completed
          steps.findIndex((step, i) => !(i in completed))
        : activeStep + 1;
        console.log('before setActiveStep ', activeStep);
    setActiveStep(newActiveStep);
    setCurrentStep(newActiveStep);
    console.log('after setActiveStep ', activeStep);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
    setCurrentStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStep = (step) => () => {
    setActiveStep(step);
    setCurrentStep(step);
  };

  const handleComplete = () => {
    const newCompleted = completed;
    newCompleted[activeStep] = true;
    setCompleted(newCompleted);
    handleNext();
  };

  const handleReset = () => {
    setActiveStep(0);
    setCompleted({});
    setCurrentStep(0);
  };

  return (
    <div className="App">
    <Box sx={{ width: '100%' }}>
     
      <Stepper nonLinear activeStep={activeStep} alternativeLabel 
         className={classes.mystyle}
        >
        {steps.map((label, index) => (
          
          <Step key={label} completed={completed[index]}  className="sam" >
          
            <StepButton  onClick={handleStep(index)} >
              {label}
            </StepButton>
           
          </Step>
          
        ))}
      </Stepper>
      <div>
     
        {allStepsCompleted() ? (
          <React.Fragment>
            <Typography sx={{ mt: 2, mb: 1 }}>
              All steps completed - you&apos;re finished
            </Typography>
            <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
              <Box sx={{ flex: '1 1 auto' }} />
              <Button onClick={handleReset}>Reset</Button>
            </Box>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <Typography sx={{ mt: 2, mb: 1 }}>Step {activeStep + 1}</Typography>
            <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
              <Button
                color="inherit"
                disabled={activeStep === 0}
                onClick={handleBack}
                sx={{ mr: 1 }}
              >
                Back
              </Button>
              <Box sx={{ flex: '1 1 auto' }} />
              <Button onClick={handleNext} sx={{ mr: 1 }}>
                Next
              </Button>
              {console.log('sam')}
              {activeStep !== steps.length &&
                (completed[activeStep] ? (
                  
                  <Typography variant="caption" sx={{ display: 'inline-block' }}>
                    Step {activeStep + 1} already completed
                  </Typography>
                  
                ) : (
                  <Button onClick={handleComplete}>
                    {completedSteps() === totalSteps() - 1
                      ? 'Finish'
                      : 'Complete Step'}
                  </Button>
                ))}
            </Box>
          </React.Fragment>
        )}
      </div>
    </Box>

    </div>  
  );
}
